.subheader {
    margin: 30px;
    color: white;
    padding: 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .breadcrumb {
    margin: 10px 0;
    font-size: 0.9em;
    color: #555;
}

.breadcrumb a {
    color: #5e3d91;
    text-decoration: none;
}

.add-btn {
    display: block;
    padding: 10px 20px;
    margin-bottom: 10px;
    background-color: #4CAF50;
    color: white;
    text-decoration: none;
    float: right;
    border-radius: 20px;
}